.scroller {
  margin: 0 auto;
  height: 100%;
  overflow: auto;
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #171616; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}
.match_value{
  color: #e43252;
  background-color: black;
}

.match_value:hover{
  color: black;
  cursor: pointer;
  background-color: #e43252;
}

.match_type_critical{
  color: #e43252;
  background-color: black;
}

.match_type_*{
  background-color: black;
}

.Match{
  padding: 0.1em;
  overflow: hidden;
  text-overflow: ellipsis;
  font-family:"Anonymous Pro";
}
.CrawlInfo{
  font-size: small;

  font-family:"Anonymous Pro";
  background-color: #202022;
  padding: 1em;
  margin-top: 1em;
  display: flex;
  flex-wrap: wrap;
  align-content: stretch;
  box-shadow: 0px 0px 0px 1px #565555;
}

h1{
  color: #9e0034;
}


verticalLine {
  border-left: thin solid #565555;
  margin-left: 1em;
  margin-right: 1em;
}
